<template>
	<div id="album">
	  <!-- 头部导航 -->
	 <div style="margin-left: 55%; transform: translateX(-50%)">
	    <HeadMenu :isNewDiscList="isNewDiscList" @changeModel="changeModel"/>
	  </div>

<!-- 歌曲标签 -->
    <div  style="position: relative; width: 100%; margin-bottom: 20px"
      v-show="isNewDiscList">
	  <!-- 子传父-->
        <discTag @getOldDisc="getOldDisc" style="width: 30%"/>
    </div>
		
	    <!-- 所有专辑速递   父传子-->
	    <oldDiscInfo :oldDiscList="oldDiscList" v-if="isNewDiscList" />
	    <!-- 新碟速递-->
	    <newDiscInfo :newDiscList="newDiscList" style="margin-top: 20px" v-else />
		
	 
	  <br /><br />
	</div>
</template>

<script>
	import HeadMenu from '../../components/Allalbum/HeadMenu.vue';
	import discTag from "../../components/Allalbum/discTag.vue";
	import newDiscInfo from "../../components/Allalbum/newDiscInfo.vue";
	import oldDiscInfo from "../../components/Allalbum/oldDiscInfo.vue";
	export default {
		name: "album",
	  components: {
	    // 顶部菜单选项
	     HeadMenu,
		 //国家选择
		 discTag,
	    // 新碟速递数据
	    newDiscInfo,
	    // 旧碟速递数据
	    oldDiscInfo,
	  },
	  data() {
	    return {
	      //新碟数据
	      newDiscList: [],
	      //旧碟数据
	      oldDiscList: [],
		  
	      //当前旧碟还是新碟
	      isNewDiscList: true,
	    };
	  },
	  methods: {
			// 通过标签获得所有专辑数据
		getOldDisc(type) {
			 var that=this;
			 // debugger;
			 this.$axios.get("https://autumnfish.cn/album/new?limit=16&area="+type)
			    .then((response) => {
			         that.oldDiscList = response.data.albums;
					},function(err){})
			},
			// 清空专辑数据
			/* cleanDiscInfo() {
			  this.oldDiscList = [];
			}, */
			
				// 获取选择，展示新碟
				changeModel(isShow) {
				  this.isNewDiscList = isShow;
				  if (!this.isNewDiscList) {
				   var that=this;
				   this.$axios.get("https://autumnfish.cn/album/newest")
				   .then(function(response){
							/* console.log(response.data.albums) */
							that.newDiscList=response.data.albums;
				   },function(err){})
				  }
				},
	    },
		
		created() {
		  this.getOldDisc('ALL');
		},
	 }
</script>

<style scoped>
	#album{
	   margin-left: 60px;
	   margin-right: 10px;
	}

</style>